<template>
  <transition name="vi-message-fade">
    <div :class="[
      'vi-message',
      type && !iconClass ? `vi-message--${type}` : '',
      center ? 'is-center' : '',
      showClose ? 'is-closable' : '',
      customClass]"
      v-show="visible"
      @mouseenter="clearTimer"
      @mouseleave="startTimer"
      role="alert">

      <slot>
        <p class="vi-message__content">
          <i v-if="iconClass"
            :class="iconClass"></i>
          <i v-else
            :class="typeClass"></i>{{message}}</p>
      </slot>
      <i v-if="showClose"
        class="vi-message__closeBtn vi-icon-close"
        @click="close"></i>
    </div>
  </transition>
</template>
<script>
const typeMap = {
  success: 'success',
  info: 'info',
  warning: 'warning',
  error: 'error'
}

export default {
  props: {},
  data() {
    return {
      visible: false,
      message: '',
      duration: 3000,
      type: 'info',
      iconClass: '',
      customClass: '',
      onClose: null,
      showClose: false,
      closed: false,
      timer: null,
      center: false
    }
  },
  computed: {
    // iconWrapClass
    typeClass() {
      return this.type && !this.iconClass
        ? `vi-message__icon vi-icon-${typeMap[this.type]}`
        : ''
    }
  },
  watch: {
    closed(newVal) {
      if(newVal) {
        this.visible = false
        this.$el.addEventListener('transitionend', this.destroyElement)
      }
    }
  },

  methods: {
    destroyElement() {
      this.$el.removeEventListener('transitionend', this.destroyElement)
      this.$destroy(true)
      this.$el.parentNode.removeChild(this.$el)
    },

    close() {
      this.closed = true
      if (typeof this.onClose === 'function') {
        this.onClose(this)
      }
    },

    clearTimer() {
      clearTimeout(this.timer)
    },

    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          if (!this.closed) {
            this.close()
          }
        }, this.duration)
      }
    },

    keydown(e) {
      if (e.keyCode === 27) {
        // esc code
        if (!this.closed) {
          this.close()
        }
      }
    }
  },

  mounted() {
    this.startTimer()
    document.addEventListener('keydown', this.keydown)
  },

  beforeDestroy() {
    document.removeEventListener('keydown', this.keydown)
  }
}
</script>



